@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
}

.app_container {
  width: 100%;
  height: 100vh;
}

.about_container {
  width: 80%;
  margin: 0 auto;
  padding: 20px 0;
  animation: slide_about 1s;
}

@keyframes slide_about {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.about_title {
  font-family: "Ubuntu", sans-serif;
  display: block;
  font-size: 32px;
  font-style: italic;
  margin-bottom: 25px;
  color: #31a5f8;
}

.about_content {
  display: flex;
  justify-content: space-around;
  font-family: "Open Sans", sans-serif;
}

.about_content img {
  width: 20%;
  height: 20%;
  border-radius: 50%;
}

.about_skill {
  font-family: "Ubuntu", sans-serif;
}

.skill_title {
  display: block;
  font-size: 32px;
  font-style: italic;
  margin-bottom: 25px;
  color: #31a5f8;
}

.skill_front {
  border-top: 2px #999999 solid;
  margin-left: 10px;
}

.front_title {
  position: relative;
  top: -14px;
  padding-right: 10px;
  font-size: 20px;
  background-color: white;
}

.front_skills {
  display: flex;
  text-align: center;
  font-family: "Open Sans", sans-serif;
}

.front_skills div {
  margin-left: 20px;
}

.front_skills span {
  margin-left: 20px;
}

