.navigation_container {
  width: 80%;
  margin: 0 auto;
  padding: 1% 0;
  font-family: "Ubuntu", sans-serif;
  font-size: 2rem;
  font-weight: bold;
  font-style: italic;
  border-bottom: 1px #b9b9b9 solid;
}

.navigation_ul {
  display: flex;
  justify-content: space-around;
}

.navigation_ul li {
  list-style: none;
  cursor: pointer;
}

.navigation_ul li:hover {
  color: black;
}

.navigation_ul li + li {
  border-left: 1px #b9b9b9 solid;
}

.color_black {
  color: black;
}

.project_container {
  width: 80%;
  margin: 0 auto;
  animation: slide_project 1s;
}

@keyframes slide_project {
  0% {
    transform: translate(-100%, 0);
  }

  100% {
    transform: translate(0, 0);
  }
}

.project {
  width: 40%;
  margin-left: 5%;
  margin-top: 5%;
  cursor: pointer;
  -webkit-filter: grayscale(100%) blur(2px);
  filter: grayscale(100%) blur(2px);
  transition: 0.3s ease-in-out;
}

.project:hover {
  -webkit-filter: grayscale(0) blur(0);
  filter: grayscale(0) blur(0);
}

.project_title {
  font-weight: bold;
  font-size: 3rem;
  background-color: #e3f2fd;
  text-align: center;
  font-family: "Goldman", cursive;
  padding: 10% 0;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  border: 1px #b9b9b9 solid;
}

.project_content {
  font-family: "Open Sans", sans-serif;
  padding: 10% 0;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  border: 1px #b9b9b9 solid;
  border-top: none;
}

.project_content span {
  margin: 0px 10px;
}

.contact_container {
  background-image: url(/portfolio/static/media/wall.840dd014.jpg);
  background-size: 100%;
  background-repeat: no-repeat;
  width: 80%;
  height: 80%;
  margin: 0 auto;
  animation: slide_contact 1s;
}

@keyframes slide_contact {
  0% {
    transform: translate(100%, 0);
  }

  100% {
    transform: translate(0, 0);
  }
}

.contact_info {
  font-family: "Open Sans", sans-serif;
  background-color: #fffbe7;
  width: 40%;
  height: 50%;
  margin: 0 auto;
  position: relative;
  top: 30%;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.contact_title {
  font-size: 30px;
  display: block;
  padding-top: 20px;
  font-weight: bold;
}

.contact_mailIcon {
  font-size: 30px;
  display: block;
  margin-top: 30px;
}

.contact_content {
  font-size: 30px;
  display: block;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: black;
}

.contact_gitIcon {
  font-size: 30px;
  display: block;
  margin-top: 20px;
}

.contact_gitContent {
  font-size: 30px;
  display: block;
}

.header_container {
  width: 80%;
  margin: 0 auto;
  background: linear-gradient(to right, #2c3e50, #bdc3c7);
  /* background: linear-gradient(to right, #0f2027, #203a43, #2c5364); */
  height: 35vh;
}

.header_content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.header_content img {
  width: auto;
  height: 90%;
  border-radius: 50%;
}

.header_intro {
  font-family: "Open Sans", sans-serif;
  font-size: 24px;
  margin-left: 20px;
}

.header_changingTitle {
  color: white;
  animation: fadeIn 2s infinite;
  display: block;
  font-weight: bold;
}

.header_title {
  color: white;
  display: block;
  margin-top: 5px;
}

.hide {
  display: none;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

